import React from 'react';
import { Row, Col, Form, Select, Input, DatePicker } from 'antd';
import { connect } from 'react-redux';
import axios from 'axios';
import moment from 'moment';

let { Option } = Select;

const CreateOrderForm = (props) => {

    let { addOrder, orderIndex, user_id, role_id } = props;
    let [designer, setDesigner] = React.useState([]);
    let [enableAssign, setEnableAssign] = React.useState(true);

    const onChange = (changedFields, allFields) => {
        if (changedFields.designType === 'Vector') {
            // getDigitizerDesigner('designer');
            setEnableAssign(true);
        } else if (changedFields.designType === 'Digitize') {
            // getDigitizerDesigner('digitizer');
            setEnableAssign(false);
        }
        addOrder(allFields, orderIndex);
    }

    // const getDigitizerDesigner = async (type) => {
    //     if (type === 'designer') {
    //         let res = await axios.post('/designerlist', { user_id });
    //         console.log(res.data.data);
    //         setDesigner(res.data.data);
    //     } else {
    //         let res = await axios.post('/digitizerlist', { user_id });
    //         console.log(res.data.data);
    //         setDesigner(res.data.data);
    //     }
    // }

    const getFile = (e) => {
        //converting object into array
        let filesObj = e.target.files;
        let arr = []
        Object.keys(filesObj).map(key => {
            arr.push(filesObj[key]);
        });
        addOrder(arr, orderIndex, 'file');
    }

    return (
        <>
            <Form
                name="basicInformation"
                layout="horizontal"
                onValuesChange={onChange}
            >
                <Row style={{ justifyContent: 'center' }}>
                    <Col xs={24} sm={24} md={24} lg={20} >
                        <Row gutter={16} align="center">
                            <Col xs={24} sm={24} md={8}>
                                <Form.Item
                                    label="Deadline"
                                    name="deadlineDate"
                                >
                                    <DatePicker disabledDate={current => current && current <= moment().subtract(1, 'days')} />
                                </Form.Item>
                            </Col>
                            <Col xs={24} sm={24} md={8}>
                                <Form.Item
                                    label="Design Name"
                                    name="designName"
                                    initialValue=''
                                    rules={[
                                        {
                                            required: true,
                                            message: 'wrong entry',
                                        },
                                    ]}
                                >
                                    <Input />
                                </Form.Item>
                            </Col>
                            <Col xs={24} sm={24} md={8}>
                                <Form.Item
                                    label="Amount Quoted"
                                    name="amountQuoted"
                                    initialValue=''
                                    rules={[
                                        {
                                            required: true,
                                            message: 'Please enter a number!',
                                        },
                                    ]}
                                >
                                    <Input />
                                </Form.Item>
                            </Col>
                            <Col xs={24} sm={24} md={8}>
                                <Form.Item
                                    label="Design Type"
                                    name="designType"
                                    initialValue=''
                                    rules={[
                                        {
                                            required: true,
                                            message: 'wrong entry',
                                        },
                                    ]}
                                >
                                    <Select >
                                        <Option value={'Vector'}>
                                            Vector
                                        </Option>
                                        <Option value={'Digitize'}>
                                            Digitize
                                        </Option>
                                    </Select>
                                </Form.Item>
                            </Col>
                            {
                                role_id == '3' &&
                                <Col xs={24} sm={24} md={8}>
                                    <Form.Item
                                        label="Assign To"
                                        name="assign_to"
                                        rules={[
                                            {
                                                required: true,
                                                message: 'wrong entry',
                                            },
                                        ]}
                                        initialValue={'-1'}
                                    >
                                        <Select disabled={enableAssign}>
                                            <Option value={'-1'}>
                                                Inhouse
                                            </Option>
                                            <Option value={'1'}>
                                                Outsource
                                            </Option>
                                            {/* {
                                                designer.map((item, index) => {
                                                    return (
                                                        <Option value={item.user_id} key={index}>
                                                            {item.user_name}
                                                        </Option>
                                                    )
                                                })
                                            } */}
                                        </Select>
                                    </Form.Item>
                                </Col>
                            }
                            <Col xs={24} sm={24} md={8}>
                                <Form.Item
                                    label="Placement"
                                    name="placement"
                                    initialValue="-"
                                    rules={[
                                        {
                                            message: 'wrong entry',
                                        },
                                    ]}
                                >
                                    <Input />
                                </Form.Item>
                            </Col>
                            <Col xs={24} sm={24} md={8}>
                                <Form.Item
                                    label="Manager Description"
                                    name="managerDescription"
                                    initialValue="-"
                                    rules={[
                                        {
                                            message: 'wrong entry',
                                        },
                                    ]}
                                >
                                    <Input />
                                </Form.Item>
                            </Col>
                            <Col xs={24} sm={24} md={8}>
                                <Form.Item
                                    label="Agent Description"
                                    name="agentDescription"
                                    initialValue="-"
                                    rules={[
                                        {
                                            message: 'wrong entry',
                                        },
                                    ]}
                                >
                                    <Input />
                                </Form.Item>
                            </Col>
                            <Col xs={24} sm={24} md={8}>
                                <Form.Item
                                    label="PO Number"
                                    name="poNumber"
                                    initialValue={0}
                                    rules={[
                                        {
                                            message: 'Please enter a number!',
                                        },
                                    ]}
                                >
                                    <Input type="number" />
                                </Form.Item>
                            </Col>
                            <Col xs={24} sm={24} md={8}>
                                <Form.Item
                                    label="Required Format"
                                    name="requiredFormat"
                                    initialValue="-"
                                    rules={[
                                        {
                                            message: 'wrong entry',
                                        },
                                    ]}
                                >
                                    <Input />
                                </Form.Item>
                            </Col>
                            <Col xs={24} sm={24} md={8}>
                                <Form.Item
                                    label="Level"
                                    name="level"
                                    initialValue="-"
                                    rules={[
                                        {
                                            message: 'wrong entry',
                                        },
                                    ]}
                                >
                                    <Input />
                                </Form.Item>
                            </Col>
                            <Col xs={24} sm={24} md={8}>
                                <Form.Item
                                    label="Fabric"
                                    name="fabric"
                                    initialValue="-"
                                    rules={[
                                        {
                                            message: 'wrong entry',
                                        },
                                    ]}
                                >
                                    <Input />
                                </Form.Item>
                            </Col>
                            <Col xs={24} sm={24} md={8}>
                                <Form.Item
                                    label="No. Of Colors"
                                    name="noofcolors"
                                    initialValue="-"
                                    rules={[
                                        {
                                            message: 'wrong entry',
                                        },
                                    ]}
                                >
                                    <Input />
                                </Form.Item>
                            </Col>
                            <Col xs={24} sm={24} md={8}>
                                <Form.Item
                                    label="Color Blending"
                                    name="colorblending"
                                    initialValue="-"
                                    rules={[
                                        {
                                            message: 'wrong entry',
                                        },
                                    ]}
                                >
                                    <Input />
                                </Form.Item>
                            </Col>
                            <Col xs={24} sm={24} md={8}>
                                <Form.Item
                                    label="Background Fill"
                                    name="backgroundFill"
                                    initialValue="-"
                                    rules={[
                                        {
                                            message: 'wrong entry',
                                        },
                                    ]}
                                >
                                    <Input />
                                </Form.Item>
                            </Col>
                            <Col xs={24} sm={24} md={24} lg={8} >
                                <Form.Item
                                    label="Height"
                                    name="height"
                                    initialValue="-"
                                    rules={[
                                        {
                                            message: 'wrong entry',
                                        },
                                    ]}
                                >
                                    <Input />
                                </Form.Item>
                            </Col>
                            <Col xs={24} sm={24} md={24} lg={8} >
                                <Form.Item
                                    label="Width"
                                    name="width"
                                    initialValue="-"
                                    rules={[
                                        {
                                            message: 'wrong entry',
                                        },
                                    ]}
                                >
                                    <Input />
                                </Form.Item>
                            </Col>
                            <Col xs={24} sm={24} md={24} lg={8} >
                                <Form.Item
                                    label="No Of Stitches"
                                    name="noOfStitches"
                                    initialValue="-"
                                    rules={[
                                        {
                                            message: 'wrong entry',
                                        },
                                    ]}
                                >
                                    <Input />
                                </Form.Item>
                            </Col>
                            <Col xs={24} sm={24} md={24} lg={24} >
                                <Form.Item
                                    label="Instructions"
                                    name="instructions"
                                    initialValue="-"
                                    rules={[
                                        {
                                            message: 'wrong entry',
                                        },
                                    ]}
                                >
                                    <Input />
                                </Form.Item>
                            </Col>
                            <Input type="file" multiple onChange={getFile} />
                        </Row>
                    </Col>
                </Row>
            </Form>
        </>
    )
}

const mapStateToProps = state => ({
    user_id: state.auth.user.user_id,
    role_id: state.auth.user.role_id,
})

export default connect(mapStateToProps)(CreateOrderForm);