import React from 'react';
import { Tabs, Card, Button, message } from 'antd';
import axios from 'axios';
import { connect } from 'react-redux';
import { useHistory } from 'react-router-dom';

import CreateOrderForm from 'components/orders/CreateOrderForm';


const { TabPane } = Tabs;

let params = {
    deadlineDate: '',
    designName: '',
    amountQuoted: '',
    designType: '',
    placement: '',
    managerDescription: '',
    agentDescription: '',
    poNumber: '',
    requiredFormat: '',
    level: '',
    fabric: '',
    noofcolors: '',
    colorblending: '',
    backgroundFill: '',
    height: '',
    width: '',
    noOfStitches: '',
    instructions: '',
    orderStatusId: '',
}

let index = 0;
const OrderForm = (props) => {
    let history = useHistory();
    let { user_id, role_id, location } = props;
    let { client_id } = location.state;

    let [orders, setOrders] = React.useState([]);
    // console.log(orders);
    const addOrder = (values, orderIndex, type) => {
        let tempOrders = orders;
        if (type === 'file') {
            tempOrders[orderIndex].attachment = values;
            setOrders(tempOrders);
        } else {
            tempOrders[orderIndex] = { ...tempOrders[orderIndex], ...values };
            setOrders(tempOrders);
        }
    }

    //state for tabs
    let [state, setState] = React.useState({
        activeTab: index,
        tabs: [{ title: 'New Order', content: <CreateOrderForm orderIndex={index} addOrder={addOrder} />, key: index }]
    });



    const onChange = activeTab => {
        setState({ ...state, activeTab });
    };

    const onEdit = (targetKey, action) => {
        if (action === 'add') {
            add(targetKey);
        } else {
            remove(targetKey)
        }
    };

    const add = () => {

        setOrders([...orders, params]);

        const { tabs } = state;
        index = index + 1;
        tabs.push({
            title: 'New Order', content: <CreateOrderForm orderIndex={index} addOrder={addOrder} />, key: index
        });
        setState({ tabs, activeTab: index });
    };

    const remove = (targetKey) => {
        let { activeTab } = state;
        let lastIndex;
        state.tabs.forEach((tab, i) => {
            if (tab.key == targetKey) {
                lastIndex = i - 1;
                //removing from orders
                let tempOrders = orders;
                tempOrders.splice(i, 1);
                setOrders(tempOrders);
            }
        });

        let tabs = state.tabs.filter(tab => tab.key != targetKey);

        if (tabs.length && activeTab == targetKey) {
            if (lastIndex >= 0) {
                activeTab = tabs[lastIndex].key;
            } else {
                activeTab = tabs[0].key;
            }
        }
        setState({ tabs, activeTab });
    };



    const saveOrder = async (type) => {
        let updatingMessage = message.loading('Creating Order', 0);
        try {
            let config = {
                header: {
                    'Content-Type': 'multipart/form-data'
                }
            }
            let formData = new FormData();
            if (type === 'save') {
                formData.append('orderstatus_id', 1);
            } else {
                if (role_id == '3') {
                    formData.append('orderstatus_id', 4);
                } else {
                    formData.append('orderstatus_id', 2);
                }
            }
            formData.append('client_id', client_id);
            formData.append('user_id', user_id);
            for (let i = 0; i < orders.length; i++) {
                if (type !== 'save' && role_id == '3' && !orders[i].assign_to) {
                    break
                } else if (type !== 'save' && role_id == '3') {
                    formData.append(`order[${i}][assign_to]`, orders[i].assign_to);
                }
                if (orders[i].attachment) {
                    for (let j = 0; j < orders[i].attachment.length; j++) {
                        formData.append(`order[${i}][attachment][${j}]`, orders[i].attachment[j]);
                    }
                }
                formData.append(`order[${i}][designName]`, orders[i].designName);
                formData.append(`order[${i}][deadlineDate]`, orders[i].deadlineDate.format('YYYY-MM-DD'));
                formData.append(`order[${i}][amountQuoted]`, orders[i].amountQuoted);
                formData.append(`order[${i}][designType]`, orders[i].designType);
                formData.append(`order[${i}][placement]`, orders[i].placement);
                formData.append(`order[${i}][managerDescription]`, orders[i].managerDescription);
                formData.append(`order[${i}][agentDescription]`, orders[i].agentDescription);
                formData.append(`order[${i}][poNumber]`, orders[i].poNumber);
                formData.append(`order[${i}][requiredFormat]`, orders[i].requiredFormat);
                formData.append(`order[${i}][level]`, orders[i].level);
                formData.append(`order[${i}][fabric]`, orders[i].fabric);
                formData.append(`order[${i}][noofcolors]`, orders[i].noofcolors);
                formData.append(`order[${i}][backgroundFill]`, orders[i].backgroundFill);
                formData.append(`order[${i}][height]`, orders[i].height);
                formData.append(`order[${i}][width]`, orders[i].width);
                formData.append(`order[${i}][noOfStitches]`, orders[i].noOfStitches);
                formData.append(`order[${i}][instructions]`, orders[i].instructions);
                formData.append(`order[${i}][orderStatusId]`, orders[i].orderStatusId);
                formData.append(`order[${i}][colorblending]`, orders[i].colorblending);
            }
            let res = await axios.post('/createorder', formData, config);
            updatingMessage();
            message.success(res.data.message);
            history.push('/orders');
        } catch (error) {
            console.log(error)
            updatingMessage();
            error.response ?
                message.error(error.response.data)
                :
                message.error(error.message)
        }
    }

    return (
        <>
            <Card>
                <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center', }}>
                    <h2>Order Form</h2>
                </div>
            </Card>
            <Card>
                <Tabs
                    onChange={onChange}
                    activeKey={state.activeTab.toString()}
                    type="editable-card"
                    onEdit={onEdit}
                    size='large'
                >
                    {state.tabs.map(tab => (
                        <TabPane tab={tab.title} key={tab.key} closable={true}>
                            {tab.content}
                        </TabPane>
                    ))}
                </Tabs>
                <div className="mr-5 mt-3">
                    <Button type="primary" htmlType="submit" style={{ float: 'right', marginRight: 10 }} onClick={() => saveOrder('proceed')}>
                        Proceed
                    </Button>
                    <Button type="primary" htmlType="submit" style={{ float: 'right', marginRight: 10 }} onClick={() => saveOrder('save')}>
                        Save
                    </Button>
                </div>
            </Card>
        </>
    )
}

const mapStateToProps = state => ({
    user_id: state.auth.user.user_id,
    role_id: state.auth.user.role_id
});

export default connect(mapStateToProps)(OrderForm);